import '../styles/Languages.css';

const Languages = () => {
  return (
    <div className="languages-box">
      <div className="languages-header">Languages &amp; Frameworks:</div>
      <div className="languages-body">
        JavaScript
        <br />
        HTML
        <br />
        CSS
        <br />
        React
        <br />
        Sass
        <br />
        Bootstrap
        <br />
        Node.js
        <br />
      </div>
    </div>
  );
};

export default Languages;



